export default class Data {
    constructor(id, data, options) {
        this.id = id;
        this.options = options;
        if (Array.isArray(data)) {
            this.rows = data;
        }
        else if (typeof data === "object") {
            this.rows = [data];
        }
        else {
            this.rows = [{ value: data }];
        }
    }
}
