export default class EventManager {
    constructor() {
        this.handlers = new Set();
    }
    Add(handler) {
        let retVal = null;
        if (!this.handlers.has(handler)) {
            this.handlers.add(handler);
            retVal = handler;
        }
        return retVal;
    }
    Remove(handler) {
        let retVal = null;
        if (this.handlers.has(handler)) {
            this.handlers.delete(handler);
            retVal = handler;
        }
        return retVal;
    }
    Trigger(args) {
        this.handlers.forEach((handler) => {
            try {
                handler(args);
            }
            catch (e) {
                console.error(e);
            }
        });
    }
}
