import ArrayToken from "../base/ArrayToken";
export default class IntegerArray extends ArrayToken {
    constructor(context, ...collection) {
        super(context, ...collection);
    }
    tryParse(value) {
        let retVal = 0;
        try {
            retVal = parseInt(value);
        }
        catch (_a) {
            /*Nothing*/
        }
        return retVal;
    }
}
