import ObjectToken from "../base/ObjectToken";
export default class IntegerObject extends ObjectToken {
    constructor(rawValue, context) {
        super(rawValue, context);
    }
    tryParse(value) {
        let retVal = 0;
        try {
            retVal = parseInt(value);
        }
        catch (_a) {
            /*Nothing*/
        }
        return retVal;
    }
}
